import React from "react";
import {
  Grid,
  Row,
  InputGroup,
  Input,
  Icon,
  Col,
  Panel,
  DatePicker,
} from "rsuite";
import { Field } from "react-final-form";
import PropTypes from "prop-types";

class ExtraPayment extends React.Component {
  handleDisableDate = (startDate) => (date) => {
    return date < startDate;
  };

  render() {
    const { formProps } = this.props;
    const formValues = formProps.form.getState().values;
    return (
      <Panel shaded>
        <Grid fluid>
          <Row>
            <Col md={6}>
              <Row>
                <Col md={24}>
                  <label>Monthly or Bi-weekly</label>
                  <Field
                    name="extraPaymentMonthlyAmount"
                    render={(fieldProps) => (
                      <InputGroup>
                        <InputGroup.Button>
                          <Icon icon="usd" />
                        </InputGroup.Button>
                        <Input {...fieldProps.input} />
                      </InputGroup>
                    )}
                  />
                </Col>
                <Col md={24}>
                  <label>Starting From</label>
                  <Field
                    name="extraPaymentMonthlyStartDate"
                    render={(fieldProps) => (
                      <InputGroup>
                        <DatePicker
                          style={{ width: 500 }}
                          disabledDate={this.handleDisableDate(
                            formValues.startDate
                          )}
                          {...fieldProps.input}
                          block
                          oneTap
                        />
                      </InputGroup>
                    )}
                  />
                </Col>
              </Row>
            </Col>
            <Col md={6}>
              <Row>
                <Col md={24}>
                  <label>Quarterly</label>
                  <Field
                    name="extraPaymentQuarterlyAmount"
                    render={(fieldProps) => (
                      <InputGroup>
                        <InputGroup.Button>
                          <Icon icon="usd" />
                        </InputGroup.Button>
                        <Input {...fieldProps.input} />
                      </InputGroup>
                    )}
                  />
                </Col>
                <Col md={24}>
                  <label>Starting From</label>
                  <Field
                    name="extraPaymentQuarterlyStartDate"
                    render={(fieldProps) => (
                      <InputGroup>
                        <DatePicker
                          disabledDate={this.handleDisableDate(
                            formValues.startDate
                          )}
                          {...fieldProps.input}
                          block
                          oneTap
                        />
                      </InputGroup>
                    )}
                  />
                </Col>
              </Row>
            </Col>
            <Col md={6}>
              <Row>
                <Col md={24}>
                  <label>Yearly</label>
                  <Field
                    name="extraPaymentYearlyAmount"
                    render={(fieldProps) => (
                      <InputGroup>
                        <InputGroup.Button>
                          <Icon icon="usd" />
                        </InputGroup.Button>
                        <Input {...fieldProps.input} />
                      </InputGroup>
                    )}
                  />
                </Col>
                <Col xs={24} md={24}>
                  <label>Starting From</label>
                  <Field
                    name="extraPaymentYearlyStartDate"
                    render={(fieldProps) => (
                      <InputGroup>
                        <DatePicker
                          disabledDate={this.handleDisableDate(
                            formValues.startDate
                          )}
                          {...fieldProps.input}
                          block
                          oneTap
                        />
                      </InputGroup>
                    )}
                  />
                </Col>
              </Row>
            </Col>
            <Col md={6}>
              <Row>
                <Col md={24}>
                  <label>One time only</label>
                  <Field
                    name="extraPaymentOnetimeAmount"
                    render={(fieldProps) => (
                      <InputGroup>
                        <InputGroup.Button>
                          <Icon icon="usd" />
                        </InputGroup.Button>
                        <Input />
                      </InputGroup>
                    )}
                  />
                </Col>
                <Col md={24}>
                  <label>Starting From</label>
                  <Field
                    name="extraPaymentOnetimeStartDate"
                    render={(fieldProps) => (
                      <InputGroup>
                        <DatePicker
                          disabledDate={this.handleDisableDate(
                            formValues.startDate
                          )}
                          {...fieldProps.input}
                          block
                          oneTap
                        />
                      </InputGroup>
                    )}
                  />
                </Col>
              </Row>
            </Col>
          </Row>
          <Row>
            <Col></Col>
          </Row>
        </Grid>
      </Panel>
    );
  }
}

ExtraPayment.propTypes = {
  formProps: PropTypes.object.isRequired,
};

export default ExtraPayment;
