import React from 'react';
import { Dropdown, Badge } from 'rsuite';

import { ReactComponent as SettingsIcon } from '../images/icons/settings-icon.svg';
import { ReactComponent as NotificationIcon } from '../images/icons/notification-icon.svg';
import '../styles/TopNav.scss';


class TopNav extends React.Component {
  render() {
    return (
      <div className="top-nav">
        <div className="container-fluid">
          <div className="left">
            <Dropdown
              title="Create New"
              appearance="primary"
              size="lg"
            >
              <Dropdown.Item>Option 1</Dropdown.Item>
              <Dropdown.Item>Option 2</Dropdown.Item>
              <Dropdown.Item>Option 3</Dropdown.Item>
              <Dropdown.Item>Option 4</Dropdown.Item>
            </Dropdown>
          </div>
          <div className="right">
            <SettingsIcon className="settings-icon" width="20" height="20" />

            <Badge className="notification-icon">
              <NotificationIcon width="20" height="20" />
            </Badge>

            <div className="user">
              <div className="icon"></div>
              <div className="name">
                Tom Jones
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TopNav;
