import React from "react";
import { connect } from "react-redux";
import { Col, Row, Grid, Button, FlexboxGrid } from "rsuite";
import { Form, FormSpy, Field } from "react-final-form";

import "../styles/DashboardPage.scss";
import "../styles/ReusableComponents.scss";
import ExtraPayment from "./calculators/ExtraPayment";
import MortgageDetails from "./calculators/MortgageDetails";
import HomeownersExpenses from "./calculators/HomeownersExpenses";
import MortgageResult from "./MortgageCalculator/widgets/MortgageResult";
import MainLayout from "./layouts/main";
import TotalPayments from "./MortgageCalculator/widgets/TotalPayments";
import withStyles from "react-jss";
import FlexboxGridItem from "rsuite/lib/FlexboxGrid/FlexboxGridItem";
import calculator from "./MortgageCalculator/calculator";
import { PAYMENT_FREQUENCY } from "./calculators/constants";
import mortgageDecorator from "./MortgageCalculator/decorator";

const styles = {
  withSpacing: {
    marginTop: 10,
    marginBottom: 10,
  },

  row1Height: {
    height: 62,
  },

  row1Col2Style: {
    height: 62,
    paddingTop: 10,
    paddingBottom: 10,
  },
};
class DashboardPage extends React.Component {
  state = {
    isExtraPayment: false,
  };

  handleSubmit(formValues) {
    const schedules = calculator.getSchedules(formValues);
    console.log(formValues);
    console.log(`Payment Schedule Deadlines: `, schedules);
  }

  handleShowExtraPayment = () => {
    this.setState((prev) => ({ isExtraPayment: !prev.isExtraPayment }));
  };

  renderForm = (formProps) => {
    const { classes } = this.props;
    const { isExtraPayment } = this.state;
    return (
      <MainLayout>
        <Grid fluid gutter={3}>
          <Row className={classes.row1Height}>
            <Col md={10}>
              <h1>Mortgage Calculator</h1>
            </Col>
            <Col md={14}>
              <Row gutter={16} className={classes.row1Col2Style}>
                <Col md={8}>
                  <Field
                    name="importListingData"
                    render={(input) => (
                      <input
                        {...input}
                        style={{
                          width: 300,
                          height: 37,
                          borderRadius: 5,
                          borderWidth: 1,
                        }}
                        type="text"
                        placeholder="Import Listing Data..."
                      />
                    )}
                  />
                </Col>
                <Col md={2}>
                  <Button color="primary" onClick={formProps.form.submit}>
                    Submit
                  </Button>
                </Col>
                <Col md={2} mdOffset={8}>
                  <Button color="green">Send</Button>
                </Col>
                <Col md={2}>
                  <Button color="primary">PDF</Button>
                </Col>
                <Col md={2}>
                  <Button color="primary">Print</Button>
                </Col>
              </Row>
            </Col>
          </Row>
          <Row>
            <Col md={10}>
              <Grid fluid gutter={3}>
                <Row gutter={4}>
                  <Col md={24} className={classes.withSpacing}>
                    <MortgageResult formProps={formProps} />
                  </Col>
                  <Col md={24} className={classes.withSpacing}>
                    <TotalPayments formProps={formProps} />
                  </Col>
                </Row>
              </Grid>
            </Col>
            <Col md={14}>
              <Grid fluid gutter={3}>
                <Row gutter={4}>
                  <Col md={24} className={classes.withSpacing}>
                    <MortgageDetails formProps={formProps} />
                  </Col>
                  <Col md={24} className={classes.withSpacing}>
                    <HomeownersExpenses formProps={formProps} />
                  </Col>
                  {isExtraPayment && (
                    <Col md={24} className={classes.withSpacing}>
                      <ExtraPayment formProps={formProps} />
                    </Col>
                  )}
                  <Col md={24} className={classes.withSpacing} justify="center">
                    <div className="addExtraPayment-container">
                      <FlexboxGrid justify="center">
                        <FlexboxGridItem colspan={4}>
                          <Button
                            color="primary"
                            onClick={this.handleShowExtraPayment}
                          >
                            Add Extra Payment
                          </Button>
                        </FlexboxGridItem>
                      </FlexboxGrid>
                    </div>
                  </Col>
                </Row>
              </Grid>
            </Col>
          </Row>
        </Grid>
        <FormSpy onChange={(formObj) => console.log(formObj.values)} />
      </MainLayout>
    );
  };

  render() {
    return (
      <Form
        decorators={[mortgageDecorator]}
        initialValues={{
          homeValue: 350000,
          downPayment: 42000,
          downPaymentPercent: 12,
          mortgageAmount: 308000,
          paymentFrequency: PAYMENT_FREQUENCY.MONTHLY,
          interestRate: 4,
          pmi_yr: 1925,
          pmi_yrPercent: 0.625,
          amortizationPeriod: "yearly",
          amortizationPeriodYr: 30,
          amortizationPeriodMo: 360,
          startDate: new Date(),
          isUsingOneTimeExpense: true,
          isUsingHomeInsurance_Yr: true,
          isUsingPropertyTaxes_Yr: true,
          oneTimeExpenses: 10500,
          oneTimeExpensesPercent: 3,
          homeInsurance_Yr: 1225,
          homeInsurance_YrPercent: 0.35,
          propertyTaxes_Yr: 4375,
          propertyTaxes_YrPercent: 1.25,
          hoaFees_Mo: 0,
          extraPaymentMonthlyAmount: 0,
          extraPaymentMonthlyStartDate: new Date(),
        }}
        keepDirtyOnReinitialize
        onSubmit={(formValues) => this.handleSubmit(formValues)}
        render={(formProps) => this.renderForm(formProps)}
      />
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isSideMenuOpen: state.common.isSideMenuOpen,
  };
};

export default connect(
  mapStateToProps,
  null
)(withStyles(styles)(DashboardPage));
